import { useContext } from 'react';
import styled, { keyframes } from 'styled-components'
import { MainContext } from '../../../App';
import phoneIcon from '@iconify-icons/akar-icons/phone';
import { Icon } from '@iconify/react';
import facebookFill from '@iconify-icons/akar-icons/facebook-fill';
import instagramFill from '@iconify-icons/akar-icons/instagram-fill';
import envelopeIcon from '@iconify-icons/akar-icons/envelope';


export default function Modal({onClose}) {
    const context = useContext(MainContext);
    const social = context.data.social
    return (
        <Overlay onClick={onClose} id='overlay'>
            <Wrapper id='modal'>
                <img src={context.data.logo.logo.url} alt={context.data.logo.logo.alt} />
                <div>
                    <p><Icon icon={phoneIcon} /><a href={`tel:+48 ${social.telefon}`}>+48 {social.telefon}</a></p>
                    <p><Icon icon={envelopeIcon} /><a href={`mailto:${social.mail}`}>{social.mail}</a></p>
                    <p><Icon icon={facebookFill} /><a href={`${social.facebook}`} target='_blank' rel='noreferrer'>{social.facebookName}</a></p>
                    <p><Icon icon={instagramFill} /><a href={`${social.instagram}`} target='_blank' rel='noreferrer'>{social.instagramName}</a></p>
                </div>
                <span onClick={onClose}>Zakmnij</span>
            </Wrapper>
        </Overlay>
    )
}
const jumpIn = keyframes`
    0%{
        transform: scale(0);
    }
    30%, 100%{
        transform: scale(1);
    }
    70%{
        transform: scale(1.1);
    }
`;
const jumpOut = keyframes`
    0%{
        transform: scale(1);
    }
    30%{
        transform: scale(1.1);
    }
    100%{
        transform: scale(0);
    }
`;
const opacity = keyframes`
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
`;
const opacityOut = keyframes`
    from{
        opacity: 1;
    }
    to{
        opacity: 0;
    }
`;
const Overlay = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0,.6);
    display: grid;
    align-content: center;
    justify-content: center;
    animation: ${opacity} .5s ease;
    &.hide{
        pointer-events: none;
        animation: ${opacityOut} .5s ease forwards;
    }
`;
const Wrapper = styled.div`
    position: relative;
    max-width: 400px;
    width: 100%;
    height: 500px;
    border-radius: 20px;
    padding: 2rem;
    margin: 0 auto;
    background-color: ${({theme}) => theme.white};
    color: ${({theme}) => theme.black};
    animation: ${jumpIn} .5s ease;
    &.hide{
        pointer-events: none;
        animation: ${jumpOut} .5s ease forwards;
    }
    img{
        width: 60%;
        margin: 0 auto;
    }
    p{
        margin-top: .7rem;
        display: flex;
        align-items: center;
        transition: color .2s ease;
        svg{
            font-size: 24px;
            margin-right: .5rem;
        }
        &:hover{
            color: ${({theme}) => theme.primary};
        }
        &:active{
            transform: scale(.97);
        }
    }
    span{
        position: absolute;
        bottom: 2rem;
        right: 2rem;
        color: ${({ theme }) => theme.primary};
        font-weight: bold;
        cursor: pointer;
    }
    @media screen and (max-width: 400px){
        width: 90%;
    }
`;