import { gql } from '@apollo/client'

export const GET_DATA = gql`
  {
    description {
      opis
    }
    photo{
      mainPhoto {
        url,
        alt,
      }
    }
    project{
      project{
        id
        url
        alt
      }
    }
    social{
      instagram
      instagramName
      facebook
      facebookName
      telefon
      mail
    }
    stopka {
      text
    }
    allServices{
      id
      usluga
      nazwa
      cena
      cenaOdDo
    }
    logo{
      logo{
        url
        alt
      }
    }
  }
`;