import styled, { keyframes } from 'styled-components'

export const opacity = keyframes`
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
`;

export const HeroImage = styled.div`
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100vh;
    background-image: url(${({src}) => src ? src : `none`});
    background-position: center;
    background-size: cover;
    &::after{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(180deg, rgba(255, 255, 255, 0.5) 0%, rgba(255, 255, 255, 0) 100%);
    }
`;
export const WelcomeScreen = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;
    max-width: 1440px;
    height: 100vh;
    margin: 0 auto;
    padding: 0 50px;
    div{
        position: relative;
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
    }
    img{
        width: 420px;
        height: 420px;
        margin: 0 auto;
        animation: ${opacity} .4s ease;
    }
    h1{
        font-size: 64px;
    }
    p.type{
        font-weight: 300;
        text-transform: uppercase;
        font-size: 14px;
    }
    @media screen and (max-width: 1100px){
        padding: 0 1rem;
        img{
            display: none;
        }
    }
`;
export const About = styled.p`
    margin: 50px 0;
    max-width: 600px;
`;
export const ProjectsContainer = styled.div`
    max-width: 1200px;
    margin: 0 auto 50px;
    a{
        font-weight: bold;
        color: ${({theme}) => theme.primary};
    }
    div.header{
        padding: 0 2rem;
        @media screen and (max-width: 768px){
            padding: 0 .5rem;
        }
    }
`;
export const Projects = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin-top: 2rem;
    div{
        max-width: 300px;
        width: 100%;
        height: 400px;
        border-radius: 20px;
        margin: 1rem;
        overflow: hidden;
        background-color: ${({theme}) => theme.primaryLight};
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
        }
    }
`;
