import React from 'react'
import { SearchGroup } from './SearchContainer'

export default function ReturnValues({choosen, services, handleSetSummary}) {
    const check = (index) => {
        handleSetSummary(index.target.value)
    }
    console.log(services)
    const handleMap = (type) => {
        return services.map(service => {
            if (service.usluga === type) {
                return (
                    <option
                    value={service.id}
                    key={service.id}>{service.nazwa}
                </option>
                )
            }
            return null;
        })
    }
    return (
        <SearchGroup className='animated'>
            <select onChange={check}>
                <option
                    value="null"
                    onClick={() => handleSetSummary(0)}>Wybierz z listy
                </option>
                {choosen === 'szycie' ? (
                    <optgroup label='Szycie'>
                        {handleMap('szycie')}
                    </optgroup>
                ) : null}
                {choosen === 'bluza' ? (
                    <optgroup label='Bluza, bluzka, koszula'>
                        {handleMap('bluza')}
                    </optgroup>
                ) : null}
            </select>
        </SearchGroup>
    )
}
