import BackgroundImage from '../../assets/marianne-krohn-8wDm3cr48Xc-unsplash.jpg'
import {Button} from '../atoms/Button'
import { SectionTitle } from '../atoms/SectionTitle'
import SearchContainer from '../organisms/SearchContainer/SearchContainer'
import {
    HeroImage,
    WelcomeScreen,
    About,
    ProjectsContainer,
    Projects,
} from './HomeScreen.styles'
import { Link } from "react-scroll";


export default function HomeScreen({data}) {
    return (
        <>
            <HeroImage src={BackgroundImage} />
            <WelcomeScreen id='top'>
                <div>
                    <h1>Elżbieta Niziołek</h1>
                    <p className='type'>KRAWCOWA</p>
                    <About>{data?.description.opis}</About>
                        <Link
                            to="search"
                            spy={true}
                            smooth={true}
                            offset={-70}
                            duration={800}>
                            <Button>
                                Zaczynajmy!
                            </Button>
                        </Link>
                </div>
                <img src={data.photo.mainPhoto.url} alt={data.photo.mainPhoto.alt} />
            </WelcomeScreen>
            <SearchContainer id='search' />
            <ProjectsContainer id='projects'>
                <div className='header'>
                    <SectionTitle>🎉 Najlepsze projekty</SectionTitle>
                    <p>Sprawdź moje wszyskie projekty na <a href={data.social.instagram} target="_blank" rel="noopener noreferrer">Instagramie</a>❤</p>
                </div>
                <Projects>
                    {
                        data.project.project.map(project => (
                            <div key={project.id}>
                                <img src={project.url} alt={project.alt} loading='lazy' />
                            </div>
                        ))
                    }
                </Projects>
            </ProjectsContainer>
        </>
    )
}
