import styled from 'styled-components'
import { Icon } from '@iconify/react';
import homeIcon from '@iconify-icons/akar-icons/home';
import scissorIcon from '@iconify-icons/akar-icons/scissor';
import faceVeryHappy from '@iconify-icons/akar-icons/face-very-happy';
import phoneIcon from '@iconify-icons/akar-icons/phone';
import { Link } from "react-scroll";
import { useState } from 'react';
import Modal from '../modal/Modal'

export default function Nav() {
    const [modalOpen, setModalOpen] = useState(false);

    const handleOpenModal = () => {
        document.body.classList.add('no-scroll');
        setModalOpen(true);
    }
    const handleCloseModal = () => {
        document.body.classList.remove('no-scroll');
        document.querySelector('#overlay').classList.add('hide');
        document.querySelector('#modal').classList.add('hide');
        setTimeout(() => {
            setModalOpen(false);
        }, 600);
    }

    return (
        <StyledNav>
            <ul>
                <li>
                    <Link
                        to="top"
                        spy={true}
                        smooth={true}
                        duration={800}>
                        <Icon icon={homeIcon} /><p>Główna</p>
                    </Link>
                </li>
                <li>
                    <Link
                        to="projects"
                        spy={true}
                        smooth={true}
                        duration={800}>
                        <Icon icon={scissorIcon} /><p>Projekty</p>
                    </Link>
                </li>
                <li><Icon icon={faceVeryHappy} /> <p>Opinie</p></li>
                <li onClick={()=>handleOpenModal()}><Icon icon={phoneIcon} /> <p>Kontakt</p></li>
            </ul>
            {modalOpen ? <Modal onClose={handleCloseModal} /> : null}
        </StyledNav>
    )
}

const StyledNav = styled.nav`
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
    background-color: rgba(0,0,0,.9);
    color: ${({theme}) => theme.white};
    width: 100%;
    height: 50px;
    padding: 0 2rem;
    box-shadow: 0px 0px 20px rgba(54, 53, 53, .2);
    ul{
        display: flex;
        flex-direction: row;
        justify-content: center;
        list-style: none;
        height: 100%;
    }
    li{
        display: flex;
        flex-direction: row;
        align-items: center;
        margin: 0 1rem;
        cursor: pointer;
        transition: color .2s ease;
        a{
            display: flex;
            flex-direction: row;
            align-items: center;
            width: 100%;
            height: 100%;
            padding: 0 .5rem;
        }
        svg{
            margin-right: .5rem;
        }
        &:hover{
            color: ${({theme}) => theme.white};
        }
        &:active{
            transform: scale(.97);
        }
    }
    @media screen and (max-width: 1000px){
        padding: 0;
        ul{
            justify-content: space-evenly;
        }
        li{
            margin: unset;
        }
    }
    @media screen and (max-width: 600px){
        li, li a{
            flex-direction: column;
            justify-content: center;
            p{
                font-size: 12px;
            }
            svg{
                margin: unset;
            }
        }
    }
`;