import React, { useContext, useState } from 'react'
import styled, {keyframes} from 'styled-components'
import ReturnValues from './ReturnValues'
import { MainContext } from '../../../App'

export default function SearchContainer({id}) {
    const [choosen, setChoosen] = useState(null);
    const [summary, setSummary] = useState(0);

    const context = useContext(MainContext);
    let services = context.services;

    const handleSetSummary = (id) => {
        if (id === 'null') {
            setSummary(0);
            return;
        }
        services.forEach(element => {
            if (element.id === id) {
                setSummary(element.cena)
            }
        });
    }
    const handleChange = (e) => {
        if (e.target.value === 'null') {
            setChoosen(null);
            setSummary(0);
            return;
        }
        setChoosen(e.target.value);
    }

    return (
        <SearchContainerDiv id={id}>
            <span><p className='title'>👗 Co Cię interesuje?</p> Sprawdź cenę interesującej Cię usługi.</span>
            <SearchBar>
                <SearchGroup>
                    <select onChange={handleChange}>
                        <option value="null">Wybierz z listy</option>
                        <optgroup label='Szycie'>
                            <option value="szycie">Szycie na miarę</option>
                        </optgroup>
                        <optgroup label='Doszywanie, skracanie, itp.'>
                            <option value="bluza">Bluza, bluzka, koszula</option>
                            <option value="zakiet">Żakiet, marynarka</option>
                            <option value="plaszcz">Płaszcz, kurtka</option>
                            <option value="spodnica">Spódnica</option>
                            <option value="sukienka">Sukienka</option>
                            <option value="spodnie">Spodnie</option>
                        </optgroup>
                        <optgroup label='Inne'>
                            <option value="akcesoria">Akcesoria dekoracyjne</option>
                        </optgroup>
                    </select>
                </SearchGroup>
                {choosen ? (
                    <>
                        <ReturnValues
                            choosen={choosen}
                            services={services}
                            handleSetSummary={handleSetSummary}
                        />
                    </>
                ) : null}
                {summary ? (
                    <>
                        <Summary><strong>Cena końcowa*:</strong> od <b>{summary} PLN</b> + <u>cena dojazdu</u></Summary>
                        <Info>*Cena końcowa jest orientacyjna i może ulec zmianie.</Info>
                    </>
                    ) : null}
            </SearchBar>
        </SearchContainerDiv>
    )
}

export const opacity = keyframes`
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
`;
export const SearchContainerDiv = styled.section`
    max-width: 1000px;
    width: 100%;
    background-color: ${({theme}) => theme.primaryLight};
    margin: 5rem auto;
    border-radius: 30px;
    padding: 2rem;
    transition: all .4s ease;
    p.title{
        font-size: 24px;
        font-weight: bold;
    }
    @media screen and (max-width: 1000px){
        border-radius: 0;
        padding: 1rem;
    }
`;
export const SearchBar = styled.div`
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    button{
        margin-left: 2rem;
    }
`;
export const SearchGroup = styled.div`
    position: relative;
    display: flex;
    flex-direction: column;
    flex: 1;
    &.animated{
        animation: ${opacity} .4s ease;
    }
    select{
        height: 50px;
        width: 100%;
        padding: 0 2rem;
        margin-top: 1rem;
        background-color: ${({theme}) => theme.white};
        border-radius: 20px;
        appearance: none;
    }
    label{
        position: absolute;
        top: -1.2rem;
        left: 1rem;
        font-size: 14px;
    }
`;
const Summary = styled.p`
    margin-top: 2rem;
    text-align: center;
    font-size: 24px;
    b{
        color: ${({theme}) => theme.green};
    }
`;
const Info = styled.p`
    margin-top: 1rem;
    font-size: 12px;
`;